import React,{useEffect} from 'react';
import './App.css';
import ReactDOM from 'react-dom';
import * as axios from 'axios';
import loading from './25.gif';

function App() {

  let arr=[]
  
  
  const add=(e)=>{
    axios({
        url: e,
        method: 'GET',
        responseType:'blob'
    })
    .then(res=>{
      const url=window.URL.createObjectURL(new Blob([res.data]))
      const link=document.createElement('a')
      link.href=link
      link.setAttribute('download','image.jpg')
      document.body.appendChild(link)
      link.click()
    })
  }


  const rand=async()=>{
    while(arr.length!==3){
      let r=Math.floor(Math.random()*1000)+1
      await fetch(`https://picsum.photos/id/${r}/info`)
      .then(res=>res.json())
      .then(d=>arr.push(d))
      .catch(err=>console.log(err))
      console.log(r);
      console.log(arr);
    }
    ReactDOM.render(
      <div id='box'>
          {arr.map(i=>{
            console.log(i.download_url);
            
            return(
              <div class='item' id={"item"+i.id} key={i.id} >
                <img src={i.download_url} alt={i.url}/>
                <div id='author'>Author: {i.author}</div>
                <button onClick={(e)=>add(e.target.id)}>Download</button>
        </div>)
          })}
        </div>,document.getElementById('app')
    )
  }


    useEffect(() => {
      rand()
    })

  return (
    <div className="App" id='app'>
        <img src={loading} alt='loading' id='loading'/>
    </div>
  );
}

export default App;
